import React from 'react';

// https://reactjs.org/docs/react-api.html#reactpurecomponent
// Be careful for the props changes that would make no use of React.memo().
export const InnerSpace = React.memo(function InnerSpace(props) {
  const { width, height, cellHeight, ...other } = props;

  const Svg = props =>
    <svg viewBox="-100 -100 200 200" width={width + 'px'} height={height + 'px'}
      style={{
        transform: props.rotate,
        opacity: .8
      }}
    >
      {props.children}
    </svg>;

  const DemoSvg = props => {
    const {r, color} = props;
    //const q = r+2;
    // <path fill="red"     d={`M${q},${q}L${q},${-q}L${-q},${-q}L${-q},${q}z`} />
    return (<g fillOpacity=".1" strokeOpacity=".5" strokeWidth="2" stroke={color} >
        <path fill="fuchsia" d={`M${r},${r}L${r},${-r}L${-r},${-r}L${-r},${r}z`} />
        <path fill="none" d={`M${r},${r}L${r},${-r}M${r},${r}L${-r},${-r}`} />
      </g>
    );
  }
  // <circle cx="0" cy="0" r="10" stroke="orange" fill="yellow" opacity={opacity} strokeWidth="1" />

  const Cvs = props => {
    const canvasRef = React.useRef(null);

    React.useEffect(() => {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext('2d');

      ctx.canvas.width  = width;
      ctx.canvas.height = height;

      ctx.lineWidth = 4;
      ctx.strokeStyle = 'red';
      ctx.fillStyle = props.fill;
      ctx.fillRect(0, 0, width, height);
      ctx.rect(0, 0, width, height);
      ctx.stroke();
    }, []);

    return <canvas id={props.id} ref={canvasRef} // width={width} height={height}
      style={{
        transformStyle: 'preserve-3d',
        position: 'absolute',
        transform: props.rotate,
        opacity: .4
      }} />;
  }

  const lns = [];
  const N = 5;  // N * 4 of <svg> and <canvas> elements will be created
  const da = 180/N;
  for (let i=0; i < N; i++) {
    lns.push( <Svg key={i * 4}     rotate={`rotateX(${i * da}deg)`}><DemoSvg r={100} color="#0ff"   /></Svg> );
    lns.push( <Svg key={i * 4 + 1} rotate={`rotateY(${i * da}deg)`}><DemoSvg r={100} color="orange" /></Svg> );
    lns.push( <Cvs key={i * 4 + 2} id={`cvs_${i}X`} rotate={`rotateX(${(i + .5) * da}deg)`} fill="blue"   /> );
    lns.push( <Cvs key={i * 4 + 3} id={`cvs_${i}Y`} rotate={`rotateY(${(i + .5) * da}deg)`} fill="green" /> );
  }

  console.log("InnerSpace", props);
  return (
    // transformStyle is required for Firefox
    <div className="inner-space" style={{
        '--in_left': -width / 2 + 'px',
        '--in_top': (-height + cellHeight) / 2 + 'px'
      }}
      {...other} >
      {lns}
    </div>
  );
});
